<template>
  <div class="container">

      <h1>Status Loteria del Gordito!</h1>
      <button class="btn btn-success findWinner" @click="findWinner" data-toggle="modal" data-target="#modalCenter">
        Encontrar ganadores 🎁
      </button>
      <button class="btn btn-info" @click="cleanFile">
        Limpiar ✨
      </button>
      <div class="form-group">
        <input type="text" v-model="totalWinners" class="form-control" aria-describedby="auxilio" placeholder="Cuantos ganadores seran? 🤔">
      </div>

      <!-- Modal -->
      <div class="modal fade" id="modalCenter" tabindex="-1" role="dialog" aria-labelledby="ModalCenterTitle" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h4 class="modal-title" id="ModalCenterTitle">Listado de ganadores 🎉</h4>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>

            <div class="modal-body">
              <div class="container">
                <div class="row">
                  <div v-for="(winner, index) in winners" :key="index">
                    <div class="col">
                      <span class="winnwer-name">{{ index + 1 }} - {{ winner.name }}</span>
                      <span class="winnwer-avatar">  {{ winner.avatar }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal">Close</button>
            </div>
          </div>
        </div>
      </div>


      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col-md-1">#</th>
            <th scope="col-md-4">Nombre</th>
            <th scope="col-md-3">Avatar</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(user, index) in userList" :key="index">
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ user.name }}</td>
            <td>{{ user.avatar }} </td>
          </tr>
        </tbody>
      </table>

  </div>
</template>

<script>

import axios from 'axios'
import { setInterval } from 'timers';

export default {
  data: function() {
    return {
      userList: "",
      winners: [],
      totalWinners : 0
    }
  },
  mounted(){ 
    console.log('testing mount')
    this.getUserList()
    // this.countDown()
  },
  methods: {
    getUserList(){
      axios({
        method: 'get',
        url: 'https://localhost/get-users'
      })
      .then( res => {
        this.userList = res.data.map( user => {
          return {
            name: user,
            avatar: this.avatar()
          }
        })
      })
      .catch(e => {
        console.log('Error ', e)
      })
    },
    avatar() {
      var emoticons = ['🐕','🐩','🐶','🦍','🐱','🐴','🐷','🦁','🦊','🐺','🐰','🐼','🐔','🦖',]
      var number = Math.floor((Math.random() * emoticons.length) );
      return emoticons[number] 
    },
    countDown(){
      var lap = 0
      setInterval( () => {
        lap= lap+1
        console.log(lap)
        if(lap == 4){
          console.log('llegamos')
          clearInterval();
        }
      },1000)
    },
    findWinner() {
      if(this.totalWinners > 0){
        this.winners = []
        var end = false
        var numberSelected = []
        var lap = 0
        while(!end){
          var number = Math.floor((Math.random() * this.userList.length) )
          if(!numberSelected.find(val => val == number)){
            numberSelected.push(number)
            this.winners.push(this.userList[number])
            lap = lap + 1
          }
          if(lap == this.totalWinners) end = true
        }
      }
      if(this.totalWinners <= 0){
        alert('Recuerda compeltar esta información')
      }
    },
    cleanFile() {
      axios({
        method: 'get',
        url: 'https://localhost/clear-list'
      })
      .then(response => {
        alert('limpiado', response)
      })
      .catch(e => {
        console.log('Error ', e)
      })
    }
  }
}
</script>

