<template>
  <div class="container" >
    <div class="text-left">
      <!-- <img src="https://secure.meetupstatic.com/s/img/5455565085016210254/logo/svg/logo--script.svg" width="180"  alt=""> -->
      <img src="../assets/logo-meetup.svg" width="180"  alt="">
      <div class="title1">AWS User Group Panamá </div>
      <div class="title2">9na edición</div>
      <div class="title4">🗓️ Miercoles 5 de junio, 2019</div>
      <div class="title3">Desplegando AWS de cero a Docker, EKS, ECS y Fargate </div>
    </div>

    <form class="register-form" v-on:submit.prevent>
      <div class="form-group">
        <label for="exampleInputEmail1">Nombre</label>
        <input v-model="name" type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Nombre, nick, apodo o como gustes!">
      </div>
      <button class="btn btn-primary" @click="validateName()">Participar</button>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data: function() {
      return {
        name: "",
      }
    },
    methods:{
      validateName(){
        var self = this;
        var nameClear = self.name.trim()
        if(nameClear == ""){
          alert(`El nombre no puede estar en blanco`)
        }
        if(nameClear != "") {
          axios({
            method: 'post',
            url: 'https://localhost/register',
            data: {
              name: self.name
            }
          })
          .then( function(response) {
            console.log(response.data)
            self.name = ""
          })
          .catch( function(e){
            console.log(e)
          })
        }
      }
    }
  }
</script>

